<template>
  <div class="tree-view-atom">
    <ul class="tree-view-atom__ul">
      <li v-for="folder of folders" :key="folder.id" class="tree-view-atom__li">
        <div :class="{
          'tree-view-atom__ul__li__divlabel-selected': false,
          'tree-view-atom__ul__li__divlabel': true
          }">
          <i v-if="folder.collapsed" class="el-icon-arrow-right"></i>
          <i v-if="!folder.collapsed" class="el-icon-arrow-down" ></i>
          <label>{{folder.name}}</label>
        </div>
        <div class="tree-view-atom__ul__li__divchildren" v-if="!folder.collapsed">
          <FileFoldersAtom :parentId="folder.id"/>
        </div>
      </li>
      <li v-for="editable of editables" :key="editable.id" class="tree-view-atom__li" @click="itemSelectedCurrent(editable.id)">
        
        <div :class="{
          'tree-view-atom__ul__li__divlabel-selected': false,
          'tree-view-atom__ul__li__divlabel': true
        }">
          <img :src="getAssetImage(editableIconMapping[editable.type])" :style="{'opacity': 0.6}"/>
          <label>{{getEditableName(editable)}}</label>
        </div>
      
      </li>
    </ul>
  </div>
</template>

<script>
import {EDITABLE_TYPES} from '../../../../../../Common/VariousTypes';
import {switchEditable} from '../../../../../../Frontend/Helpers/editableHelpers';

import FileFoldersAtom from './FileFoldersAtom.hook'
const hook = new FileFoldersAtom();

const editableIconMapping = {
  [EDITABLE_TYPES.MODULE]:"puzzle-piece-solid.svg",
  [EDITABLE_TYPES.API]:"api.svg",
  [EDITABLE_TYPES.DOCUMENT_LEVEL]:"globe-solid.svg",
  [EDITABLE_TYPES.ADMIN]:"adminscript.svg",
  [EDITABLE_TYPES.CRON]:"clock-regular.svg",
  [EDITABLE_TYPES.SCHEMA]:"schema.svg",
  [EDITABLE_TYPES.SCRIPT]:"python-brands.svg",
  [EDITABLE_TYPES.SQL]:"file-sql.svg",
  [EDITABLE_TYPES.HTML]:"file-html.svg",
  [EDITABLE_TYPES.JSON]:"file-js.svg",
  [EDITABLE_TYPES.CSV]:"file-csv.svg",
  [EDITABLE_TYPES.TXT]:"file-csv.svg"
}

const getEditableName = (editable)=>{

if (editable){
    switch(editable.type){
      case EDITABLE_TYPES.API:
        return editable.properties['0'].path;
      case EDITABLE_TYPES.DOCUMENT_LEVEL:
        return "All Requests";
      default:
        return editable.properties['0'].name;
    }
  }
}


const sortCompare = (a, b) => {
  const editableA = getEditableName(a).toUpperCase();
  const editableB = getEditableName(b).toUpperCase();

  let comparison = 0;
  if (editableA > editableB) {
    comparison = 1;
  } else if (editableA < editableB) {
    comparison = -1;
  }
  return comparison;
}

export default {
  name: 'FileFoldersAtom',
  beforeCreate() { hook.hook(this)},
  methods: {
    getEditableName,
    itemSelectedCurrent(editableId){
      const editable = this.openedDocument.editables.find(editable => editable.id == editableId);
      if (editable){
        switchEditable(hook,this,editable);
      }     
    },
    // itemSelectedChild(item,$event){
    //   $event.trace.push(item.id);
    //   this.$emit("itemSelected", $event);
    // },
    getAssetImage(image) {
        if (image){
          const images = require.context('../../../../../../Frontend/public/assets/images/');
          return images(`./${image}`);
        }
    }
  },
  components: {
    FileFoldersAtom: this
  },
  props: { 
    parentId:String,
  },
  data() {
      return {
        editableIconMapping
      }
  },
  computed: {
    folders(){
        return this.openedDocument?.folders?.filter(folder => folder.parentId == this.parentId) || [];
    },
    editables(){
      if (this.parentId == null){
        return this.openedDocument?.editables?.filter(editable => !editable.folderId).sort(sortCompare) || [];
      }else {
        return this.openedDocument?.editables?.filter(editable => editable.folderId == this.parentId).sort(sortCompare) || [];
      }
    },
    ...hook.getters("openedDocument")
  }
}

</script>

<style scoped lang="scss">

.tree-view-atom {
  border-left: 1px dotted rgb(100, 100, 100);

  &__ul {
    list-style:none;
    color: rgb(202, 202, 202);
    font-size:14px;
    padding-left:25px;
    &__li {
      

      @mixin divLabel(){
        
        transition: 0.25s;
        display: flex;
        align-items: center;
        img{
          max-height:20px;
          
          margin-bottom:4px;
          margin-right:10px;
          margin-left:-8px;
        }

        i{
          font-size:16px;
          font-weight: bold;
          margin-top:4px;
          margin-bottom:4px;
          margin-left:-8px;
          margin-right:10px;
        }

        label {
          cursor: pointer;
        }
      }

      &__divlabel{
        @include divLabel();
      }

      &__divlabel-selected {
        @include divLabel();
        color: rgb(127, 219, 127);
      }

    }
  }

}

</style>
